<template>
  <v-main class="pa-0 mt-6">
    <v-row>
      <v-col cols="12" md="9">
        <v-card class="mb-3">
          <v-card-title class="pa-1">
            <v-icon
                large
                left
                color="blue"
            >
              mdi-clipboard-list
            </v-icon>
            How to use EasyGTM
          </v-card-title>
          <v-card-text>
            <v-timeline
                align-top
                :dense="$vuetify.breakpoint.smAndDown"
            >
              <v-timeline-item
                  v-for="(step, i) in steps"
                  :key="i"
                  :color="step.color"
                  :icon="step.icon"
                  fill-dot
              >
                <v-card
                    :color="step.color"
                    dark
                >
                  <v-card-title class="text-h6">
                    {{ step.title }}
                  </v-card-title>
                  <v-card-text class="white text--primary pt-3">
                    <p v-html="step.text"></p>
                  </v-card-text>
                </v-card>
              </v-timeline-item>
            </v-timeline>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" md="3">
        <div class="text-center" style="margin-bottom: -13px;">
          <v-icon
              large
              color="cyan"
          >
            mdi-heart
          </v-icon>
        </div>

        <v-sheet
            color="cyan"
            rounded
            class="pa-3 mb-6"
        >
          <p class="text-center text--secondary ma-0">
            If you want me to assist you in the process of using this platform, please consider giving a small donation,
            and don't forget to include your email. I will get in touch ASAP.<br>
            Remember, you will be helping many stray animals in Cuba.<br>
            <v-icon small>
              mdi-cat
            </v-icon>
            <v-icon small>
              mdi-heart
            </v-icon>
            <v-icon small>
              mdi-dog
            </v-icon>
          </p>
        </v-sheet>

        <div class="text-center mb-4">
          <Donate></Donate>
        </div>

        <v-divider class="mb-6 info"></v-divider>
      </v-col>
    </v-row>
  </v-main>
