<script>
import Donate from "@/components/Donate.vue";

export default {
  name: "Howto",
  title: 'How-To',
  components: {Donate},
  data() {
    return {
      steps: [
        {
          title: "Get your Container ID",
          text: "Go to <a href='https://tagmanager.google.com'>https://tagmanager.google.com</a> and locate the account you want to use. Then, copy the <em>Container ID</em> value, normally in the format <strong>GTM-XXXXXXX</strong>",
          icon: "mdi-numeric-1",
          color: "blue",
        },
        {
          title: "Configure the tags you want to use",
          text: "Go to <a href='/'>EasyGTM</a> and enter the copied <em>Container ID</em> in the proper input field. Then activate the different tags you want to launch in your workspace, this will display the configuration form for each tag. Provide the requested data.",
          icon: "mdi-numeric-2",
          color: "blue darken-1",
        },
        {
          title: "Generate the workspace json file",
          text: "Once you finished configuring the tags you can click on the <strong>GENERATE WORKSPACE.JSON</strong> button. This will generate the script you need to import in your Google Tag Manager container. The script will appears in a textarea located below the tags configuration cards. You can copy it or simply download as a json file.",
          icon: "mdi-numeric-3",
          color: "blue darken-2",
        },
        {
          title: "Import the workspace in your GTM account",
          text: "Import the generated <em>workspace.json</em> file in your Google Tag Manager account. Check the <strong>Overwrite</strong> option. Don't forget to publish your workspace.",
          icon: "mdi-numeric-4",
          color: "blue darken-3",
        },
        {
          title: "Set marketing and statistics cookies",
          text: "Set two cookies: <code>easygtm_marketing_accepted</code> and <code>easygtm_statistics_accepted</code>, both to the \"accepted\" value. If you need to be GDPR compatible you will need to allow users to accept or deny these cookies, maybe using an acceptance notice.",
          icon: "mdi-cookie-settings",
          color: "success darken-1",
        },
        {
          title: "Insert scripts and dataLayer",
          text: "Go to the site you want to track (maybe your storefront) and insert the scripts required by <strong>Google Tag Manager</strong>. Remember to generate and insert the proper <code>dataLayer</code> variable. You can check the <a href='/datalayer-reference'>dataLayer reference</a> section to know what to include.",
          icon: "mdi-lightbulb-on",
          color: "amber darken-1",
        },
      ],
    };
  },
}
</script>

